import React from "react";
import { Link } from "react-router-dom";
import FontAwesome from "react-fontawesome";
import { connect } from "react-redux";

const SidenavItems = ({ user }) => {
    const items = [
        {
            type: "navItem",
            icon: "home",
            text: "",
            link: "/",
            restricted: false
        },
        {
            type: "navItem",
            icon: "file-text-o",
            text: "",
            link: "/user",
            restricted: true
        },
        {
            type: "navItem",
            icon: "file-text-o",
            text: " ",
            link: "/user/register",
            restricted: true
        },
        {
            type: "navItem",
            icon: "file-text-o",
            text: "",
            link: "/login",
            restricted: false,
            exclude: true
        },
        {
            type: "navItem",
            icon: "file-text-o",
            text: " ",
            link: "/user/user-reviews",
            restricted: true
        },
        {
            type: "navItem",
            icon: "file-text-o",
            text: " ",
            link: "/user/add",
            restricted: true
        },
        {
            type: "navItem",
            icon: "file-text-o",
            text: "",
            link: "/user/logout",
            restricted: true
        }
    ];

    const element = (item, i) => (
        <div key={i} className={item.type}>
            <Link to={item.link}>
                <FontAwesome name={item.icon} />
                {item.text}
            </Link>
        </div>
    );

    const showItems = () =>
        user.login
            ? items.map((item, i) => {
                  if (user.login.isAuth) {
                      //  ,      "/login",
                      return !item.exclude ? element(item, i) : null;
                  } else {
                      //   ,    restricted=false   
                      return !item.restricted ? element(item, i) : null;
                  }
              })
            : null;

    return <div>{showItems()}</div>;
};

function mapStateToProps(state) {
    return {
        user: state.user
    };
}

export default connect(mapStateToProps)(SidenavItems);



